import './sidebar.css';
import TopicsList from './topics-list';


const Sidebar = (props: any) => {

  return (
    <div className={props.class}>
      <TopicsList  index={props.index} cardsArray={props.cardsArray}/>
      <button type="button" className="sidebar__button" onClick={props.showLogin}>Login</button>
    </div>
  )
}

export default Sidebar;
