import React from 'react';
import './main.css';
import { Link, withRouter } from 'react-router-dom';
import {Star, StarLose} from './star';

let cardColor = 'card_train';
let el: any;
let rightCard: string;
let server: string;

function reverseCard() {
  el.classList.remove('flipped');
  el.removeEventListener('mouseleave', reverseCard);
}
function  getEvent (e: any) {
  if (e.target.className === 'card__button') {
    el = e.currentTarget.closest('.card-container');
    el.className += ' flipped';
    el.addEventListener('mouseleave', reverseCard);
  } else playSound(server + e.currentTarget.dataset.audio);
}
function playSound(addr: string) {
  const audio = new Audio(addr);
  audio.currentTime = 0;
  audio.play();
}
function repeatSound(addr: string) {
  const buttonStart = document.querySelector('.main__button');
  buttonStart?.classList.add('main__button_push');
  setTimeout(() => {
    buttonStart?.classList.remove('main__button_push');
    }, 500);
  setTimeout(() => {
    playSound(addr)
    }, 600);
}

interface IProps {
  index: string,
  train: boolean,
  startGame: () => any,
  gameStarted: boolean,
  match: any,
  location: any,
  history: any,
  setTrain: () => any,
  cardsArray: any[],
  server: string,
}
interface IState {
  starsList: string[],
  buttonStart: boolean,
  gamePlay:boolean,
  cardsArray:any[],
  cards:any[],
  playedSoundAddr: string,
  cardDelete: number,
  categoryList: string[],
}

class Main extends React.Component<IProps, IState> {

  constructor(props: IProps) {
    super(props);
    this.state = {
        cardDelete: 0,
        starsList: [],
        playedSoundAddr: '',
        buttonStart: false,
        gamePlay: false,
        cardsArray: [...this.props.cardsArray],
        cards: [...this.props.cardsArray[Number(this.props.index)]] as {word: string, translation: string, image: string, audioSrc: string}[],
        categoryList: [...this.props.cardsArray[0]],
     }
  }

  playGame =() => {
    let cardsCategory =[...this.state.cards];
    const randomNumber = Math.floor(Math.random() * cardsCategory.length);
    const addr = (typeof cardsCategory[randomNumber] === 'object') ? server + cardsCategory[randomNumber].audioSrc : '';
    setTimeout(() => playSound(addr), 700);
    rightCard = cardsCategory[randomNumber].word;
    this.setState({
      playedSoundAddr: addr,
      cardDelete: randomNumber,
    })
  }
  checkCard = (e: any) => {
    if (this.state.gamePlay) {
      if (e.target.alt === rightCard) {
        const cardsCategory = [...this.state.cards] as {word: string, translation: string, image: string, audioSrc: string}[];
        cardsCategory.splice(this.state.cardDelete, 1);
        this.setState({
        cards: [...cardsCategory],
        })
        playSound(process.env.PUBLIC_URL + '/audio/correct.mp3');
        e.target.closest('.card').classList.add('card_guessed');
        this.addStar(<Star />);
        setTimeout(() => { if (cardsCategory.length > 0) this.playGame();})
      } else {
        playSound(process.env.PUBLIC_URL + '/audio/error.mp3');
        this.addStar(<StarLose />);
      }
    }
  }
  addStar = (el: any) => {
    this.setState((state: any) => ({
      starsList: [el, ...state.starsList]
    }))
  }
  getCategoryList = () => {
    return (
      this.state.categoryList.map((item, index) => {
        const arr = [...this.state.cardsArray[index + 1]] as {word: string, translation: string, image: string, audioSrc: string}[];
        const addrImage = server + arr[0].image;
          return (
            <div className="card-container" key={arr[0].word}>
              <Link to={"/main/" + (index + 1)} className={'card ' + cardColor} key = {item.toString()}>
                <img src={addrImage} alt={arr[0].word} className="card__image_back"></img>
                {item}
              </Link>
            </div>
          );
      })
    )
  }
  getGameList = (i: string, train: boolean) => {
    const indexCategory = Number(i) === 0 ? 1 : Number(i);
    return (
      this.state.cardsArray[indexCategory].map((item: any) => {
        const arr = item as {word: string, translation: string, image: string, audioSrc: string};
        const addrImage = server + arr.image;
        if (train) {
          return (
            <div className="card-container" key={arr.word}>
              <div className="card card_eng" data-audio={arr.audioSrc} onClick={getEvent}>
                <img src={addrImage} alt={arr.word} className="card__image_front"></img>
                {arr.word}
                <div className="card__button"></div>
              </div>
              <div className="card card_ru">
                <img src={addrImage} alt={arr.word} className="card__image_front"></img>
                {arr.translation}
              </div>
            </div>
          );
        } else {
          return (
            <div className="card-container" key={arr.word}>
              <div className="card" data-audio={arr.audioSrc}>
                <img src={addrImage} alt={arr.word} className="card__image_front" onClick={(e: any) => this.checkCard(e)}></img>
              </div>
            </div>
          );
        }
      })
    )
  }

  render() {

    server = this.props.server;
    const index = this.props.index;
    let train = this.props.train;
    // console.log('render main',this.props);
    // console.log('render main',this.state);


    cardColor = train ? 'card_train' : 'card_game';
    if (train) rightCard = '';
    //const gameList = getGameList(index, train);
    //const categoryList = this.getCategoryList();
    const list = index === '0' ? this.getCategoryList() : this.getGameList(index, train);
    const button = (train || index === '0') ? 'main__button hidden' : 'main__button';
    if(this.props.gameStarted && !this.state.gamePlay && Number(this.props.index)) {
      this.setState({
        gamePlay: true,
        cards: [...this.props.cardsArray[Number(this.props.index)]],
      });
      setTimeout(() => this.playGame(), 100) 
    } else if ((!this.props.gameStarted && this.state.gamePlay)) {
      this.setState({
        gamePlay: false,
        cards: [...this.props.cardsArray[Number(this.props.index)]],
        starsList: [],
      });
    }
    if (this.state.cards.length){
    if (this.state.gamePlay) {
      return (
        <div className="main_wrapper">
          <div className="star-container">
          <div className="star-container_wrapper">
          {this.state.starsList}
          </div></div>
          <main className="main">
            {list}
          </main>
          <button className="main__button main__button_repeat" onClick={() => repeatSound(this.state.playedSoundAddr)}></button>
        </div>
      )
    } else {
        return (
          <div className="main_wrapper">
            <main className="main">
              {list}
            </main>
            <button className={button} onClick={this.props.startGame}></button>
          </div>
        )
      }} else {
          if(this.state.starsList.length === 8) {
            playSound(process.env.PUBLIC_URL + '/audio/success.mp3');
            setTimeout(() => {
              this.setState({cards: [...this.props.cardsArray[Number(this.props.index)]],
            })
              this.props.setTrain();
              this.props.history.push('/');
            }, 3000)
            return(
              <div className="win-lose-image_wrapper">
                <img src={process.env.PUBLIC_URL + '/image/success.jpg'} alt="success" className="win-lose-image"></img>
              </div>
            );
          } else {
              playSound(process.env.PUBLIC_URL + '/audio/failure.mp3');
              setTimeout(() => {
                this.setState({cards: [...this.props.cardsArray[Number(this.props.index)]],});
                this.props.setTrain();
                this.props.history.push('/');
              }, 3000)
              return(
                <div className="win-lose-image_wrapper">
                  <img src={process.env.PUBLIC_URL + '/image/failure.jpg'} alt="failure" className="win-lose-image"></img>
                  <span className="win-lose-image__text">You have {this.state.starsList.length - 8} mistake(s)!</span>
                </div>
              );
          }
      }
  }
}

export default withRouter(Main);
