import React from 'react';
import './login.css';
//import {showLogin} from './../user/sidebar';


function Login(props: any) {
  const classLoginWrapper = props.log ? "login__wrapper" : "login__wrapper hidden";
  return (
    <div className={classLoginWrapper}>
      <div className="login">
        <h2 className="login__h2">
          Login
        </h2>
        <input type="text" id="input-login" className="login__input" placeholder="login (admin)"/>
        <input type="password" id="input-password" className="login__input" placeholder="password (admin)"/>
        <div className="login__footer">
          <button type="button" id="button-cancel" className="login__button login__button_cancel" onClick={props.showLogin}>Cancel</button>
          <button type="button" id="button-login" className="login__button" onClick={props.sendLogin}>login</button>
        </div>
      </div>
    </div>
  )
}

export default Login;
