import React from 'react';
import { BrowserRouter as Router, Route, Redirect, Switch} from 'react-router-dom';
import Header from './header'
import Main from './main';
import './main.css';
import Footer from './footer';
import Login from './../admin/login';
import AdminPage from './../admin/admin-page';

interface IProps {
  train: boolean,
  sidebar?: boolean,
}
interface IState {
  sidebarHidden: boolean,
  gameStarted: boolean,
  train: boolean,
  cardsArray: any[],
  log: boolean,
  adminPage: boolean,
  server: string,
}

class App  extends React.Component<IProps, IState> {

  constructor (props:any) {
    super(props);
    this.state = {
      sidebarHidden: true,
      gameStarted: false,
      train: true,
      cardsArray: [],
      log: false,
      adminPage: false,
      server: 'https://sheltered-shore-31833.herokuapp.com/',
    }
    this.getCardsArray();
  }

  getCardsArray() {
    fetch(this.state.server + 'main')
      .then((response) => response.json())
      .then((categories) => {
        this.setState({
          cardsArray: [...categories]
        })
      })
  }
  sendLogin = () => {
    const log = document.getElementById('input-login') as HTMLInputElement;
    const pass = document.getElementById('input-password') as HTMLInputElement;
    const user = {
      l: log.value,
      p: pass.value,
    }
    fetch(this.state.server + 'login', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json;charset=utf-8'
      },
      body: JSON.stringify(user)
    }).then((res) => {
      if (!res.ok) {
        res.text().then((res) => alert(res))
      } else {
        this.setState((state) => ({
          adminPage: !state.adminPage
        }))
      }
    })
  }
  deleteCategory = (e: any) => {
    const id = e.target.dataset.id;
    fetch(this.state.server + `categories/${id}`, {
      method: 'DELETE',
      headers: {
        'Content-Type': 'application/json;charset=utf-8'
      },
    }).then((res) => res.json())
      .then((res) => {
        this.setState({
          cardsArray: [...res]
        })
      })
  }
  sendUpdateCategory  = (id: string, value: string) => {
    //const id = e.target.dataset.id;
    const categoryName = {category: value}
    fetch(this.state.server + `categories/${id}`, {
      method: 'PUT',
      headers: {
        'Content-Type': 'application/json;charset=utf-8'
      },
      body: JSON.stringify(categoryName)
    }).then((res) => res.json())
      .then((res) => {
        this.setState({
          cardsArray: [...res]
        })
      })
  }
  playSound(addr: string) {
    const audio = new Audio(process.env.PUBLIC_URL + '/' + addr);
    audio.currentTime = 0;
    audio.play();
  }
  startGame = () => {
    const buttonStart = document.querySelector('.main__button');
    buttonStart?.classList.add('main__button_push');
    setTimeout(() => {
      buttonStart?.classList.remove('main__button_push');
      }, 500);
    setTimeout(() => {
      this.setState({
        gameStarted: true,
      })
    }, 600)
  }
  setTrain = () => {
    this.setState((state) => ({
      train: !this.state.train,
    }));
  }
  hideSidebar = (e: any): void => {
    if (this.state.sidebarHidden === false 
      && this.state.gameStarted) {
        const links = document.querySelectorAll('.sidebar__link');
        links.forEach((item) => {
          if (e.target === item) {
            this.setState({
              gameStarted: false,
            });
          }
        })
    } 
    if (e.target !== document.querySelector('.sidebar')
      && e.target !== document.querySelector('.sidebar__list')
      && e.target !== document.querySelector('.sidebar__button')
      && this.state.sidebarHidden === false) {
        this.setState({
          sidebarHidden: !this.state.sidebarHidden,
        });
    } else if (e.target === document.querySelector('.menu-toggle')) {
      this.setState({
        sidebarHidden: !this.state.sidebarHidden,
      });
    }
  }
  showLogin = () => {
    this.setState((state) => ({
      log: !state.log
    }))
  }

  render() {
    const train = this.state.train;
    if (train && this.state.gameStarted) {
      this.setState({
        gameStarted: false,
      })
    };
    
    if (this.state.cardsArray[0]) {
      if ( this.state.adminPage) {
        return (
          (<Router>
            <Redirect from="/" to="/admin/categories" />
            <Route path="/admin/categories" exact> 
              <AdminPage cards={this.state.cardsArray} deleteCategory={this.deleteCategory} sendUpdateCategory={this.sendUpdateCategory}/>
            </Route>
          </Router>)
        )
      } else {
        return(
          <Router>
            <div className="app" onClick={this.hideSidebar}>
              <Route path="/" exact> 
                <div>
                  <Header index="0" train={train} toggleSidebar={this.hideSidebar} sidebar={this.state.sidebarHidden} setTrain={this.setTrain} showLogin={this.showLogin} cardsArray={this.state.cardsArray[0]}/>
                  <Main train={train} index="0" startGame={this.startGame} gameStarted={this.state.gameStarted} setTrain={this.setTrain} cardsArray={this.state.cardsArray} server={this.state.server}/>
                  <Login showLogin={this.showLogin} log={this.state.log} sendLogin={this.sendLogin} />
                  <Footer />
                </div>
              </Route>
              <Route path="/main/:id" render={({match}) => {
                const id = match.params.id;
                return (
                  <div>
                    <Header index={id} train={train} toggleSidebar={this.hideSidebar} sidebar={this.state.sidebarHidden} setTrain={this.setTrain} showLogin={this.showLogin} cardsArray={this.state.cardsArray[0]}/>
                    <Main index={id} train={train} startGame={this.startGame} gameStarted={this.state.gameStarted} setTrain={this.setTrain} cardsArray={this.state.cardsArray} server={this.state.server}/>
                    <Login showLogin={this.showLogin} log={this.state.log} sendLogin={this.sendLogin}/>
                    <Footer />
                  </div>
                )
              }} />
            </div>
          </Router>
        )
      }
    } else {
      return (
        <div>
          <span>Connection...</span>
        </div>
      )
    }
    }
  }

  export default App;
