import React from 'react';
import './admin-page.css';
import { Link } from 'react-router-dom';
import CardCategory from './card-category';

function AdminPage(props: any) {
  const cards = [...props.cards];
  const categories = cards[0].map((item: string, index: number) => {
    return (
      <CardCategory id={index} item={item} cards={cards[index + 1]} update={false} deleteCategory={props.deleteCategory} sendUpdateCategory={props.sendUpdateCategory}/>
    )
  })

  return (
    <div className="admin">
      <header className="admin__header">
        <nav className="header__nav">
          <Link to="/admin/categories" className="header__link">Categories</Link>
          <Link to="#" className="header__link del-underline">Words</Link>
        </nav>
        <button className="header__button">Log out</button>
      </header>
      <main className="admin__main">
        {categories}
        <div className="card-category card-category_new">
          <h3 className="card-category__title">Create new Category</h3>
          <div className="card-category__button-add-new"></div>
        </div>
      </main>
    </div>
  )
}

export default AdminPage;
