import React from 'react';

interface IProps {
  id: number,
  item: string,
  update: boolean,
  cards: Array<{word: string, translation: string, image: string, audioSrc: string}>
  deleteCategory: () => void,
  sendUpdateCategory: (id: string, value: string) => void,
}
interface IState {
  update: boolean,
}

class CardCategory extends React.Component<IProps, IState> {

  constructor(props: any) {
    super(props);
    this.state = {
      update: false,
    }
  }

  updateCategory = () => {
    //const id = e.target.dataset.id;
    this.setState ((state) => ({
      update: !state.update
    }))
  }
  createUpdateCategory = (e: any) => {
    const id = e.target.dataset.id;
    const input = document.getElementById(`card-category__input-${id}`) as HTMLInputElement;
    this.props.sendUpdateCategory(id, input.value);
    this.updateCategory();
  }

  render() {
    const {id, item, cards} = this.props;
    //const 
  if (!this.state.update) {
    // change mode
    return (
      <div className="card-category" key={item}>
        <div className="card-category__delete" onClick={this.props.deleteCategory} data-id={id}></div>
        <h3 className="card-category__title">{item}</h3>
        <div className="card-category__text">WORDS: {cards.length}</div>
        <div className="card-category__buttons-container">
          <button className="card-category__button" data-id={id} onClick={this.updateCategory}>Update</button>
          <button className="card-category__button" data-id={id}>Add word</button>
        </div>
      </div>
    )
  } else {
    // view mode
    return (
      <div className="card-category" key={item}>
        <div className="card-category__delete" onClick={this.props.deleteCategory} data-id={id}></div>
        <div className="card-category__small-text">Category Name</div>
        <input type="text" className="card-category__input" placeholder={item} id={'card-category__input-'+id}></input>
        <div className="card-category__buttons-container">
          <button className="card-category__button card-category__button_update card-category__button_cancel" data-id={id} onClick={this.updateCategory}>Cancel</button>
          <button className="card-category__button card-category__button_update" data-id={id} onClick={this.createUpdateCategory}>Create</button>
        </div>
      </div>
    )
    }
  }
}

export default CardCategory;